// Products

.products {
  padding: 80px 0;

  @include Sm {
    padding: 60px 0 70px;
  }

  h2 {
    margin: 0;
    font-size: 24px;
    font-family: "tjMd";
    color: #333;
    text-align: center;
    margin-bottom: 30px;

    @include Xs {
      font-size: 20px;
    }

    &:before {
      content: url("../images/sh.png");
      transform: translateY(5px);
      display: inline-block;
      margin-left: 10px;
      @include LTR {
        margin-left: 0;
        margin-right: 10px;
        transform: scaleX(-1) translateY(5px);
      }
    }

    &:after {
      content: url("../images/sh.png");
      transform: scaleX(-1) translateY(5px);
      display: inline-block;
      margin-right: 10px;
      @include LTR {
        margin-right: 0;
        margin-left: 10px;
        transform: translateY(5px);
      }
    }
  }

  .owl-carousel .owl-item img {
    width: auto;
  }

  .owl-nav {
    top: 40%;
    display: flex !important;
    width: calc(100% + 86px);

    @include Md {
      width: calc(100% + 40px);
    }

    @include Lg {
      width: calc(100% + 86px);
    }

    @include Sm {
      display: none !important;
    }

    span {
      font-size: 18px;
      color: #333;
      transition: 0.5s;

      &:hover {
        color: #ffb611;
      }
    }
  }

  .owl-dots {
    @include Md {
      display: none;
    }

    @include Sm {
      display: flex;
      bottom: -40px;
    }
    span {
      width: 9px;
      height: 9px;
      border-radius: 50%;
      background-color: rgba($color-black, 0.4);
    }

    .owl-dot.active {
      span {
        background-color: #01643e;
      }
    }
  }
}

.product {
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  @include LTR{
    align-items: flex-end;
  }

  @include Xs {
    align-items: center !important;
  }

  .pic {
    width: 210px;
    height: 202px;
    @include Flex;
    margin: auto;
    position: relative;
    background-color: #fff;
    margin-bottom: 17px;
    overflow: hidden;

    img {
      transition: 0.5s;
    }

    @include Xs {
      width: auto;
    }
  }

  .title {
    font-size: 15px;
    color: #333;
    text-align: center;
    margin-bottom: 10px;
    text-indent: 20px;

    @include Xs {
      text-indent: 0px;
    }

    @include LTR {
      direction: ltr;
    }
  }

  .priceSec {
    @include Flex;

    @include LTR{
      direction: ltr;
    }

    .desc {
      font-size: 16px;
      font-family: "tjMd";
      color: #afafaf;
      text-decoration: line-through;

      @include LTR {
        direction: ltr;
      }
    }

    .price {
      text-indent: 20px;
      font-size: 18px;
      font-family: "tjMd";
      color: #03653e;

      @include Xs {
        text-indent: 0px;
      }

      @include LTR {
        direction: ltr;
      }
    }
    > div {
      &:not(:last-child) {
        margin-left: 10px;

        @include LTR {
          margin-right: 10px;
          margin-left: 0;
        }
      }
    }
  }

  // &:hover {
