    transform: scale(1);
  }
  100% {
    transform: scale(1.05);
  }
}

.banner1 {
  .col-sm-6 {
    &:not(:last-child) {
      @include Xs {
        margin-bottom: 25px;
      }
    }
  }
  img {
    @include Xs {
      margin: auto;
    }
  }
}

.banner1,
.banner2 {
  a {
    display: block;
    width: 100%;
    height: 100%;
    overflow: hidden;

    &:hover {
      img {
        animation: scale 3s linear;
      }
    }
  }
}

