// Features

.features {
  margin: 55px 0;

  @include Xs {
    margin: 25px 0 40px;
  }

  .col-md-3 {
    html[dir="ltr"] & {
      padding: 0 3px;
    }
  }

  .feat {
    @include Flex;

    span {
      &.lnr {
        font-size: 24px;
        color: #caac66;
        margin-left: 10px;
        html[dir="ltr"] & {
          margin-left: 0;
          margin-right: 10px;
        }
      }

      &:last-child {
        font-size: 15.7px;
        color: #333;

        // @include mob600{
        //   width: 28%;
        // }

        // @include mob480{
        //   width: 35.5%;
        // }

        // @include mob375{
        //   width: 46.1%;
        // }

        // @include mob360{
        //   width: 48.2%;
        // }
      }
    }

    @include Sm {
      margin-top: 20px;
      flex-direction: column;

      span {
        margin: 0;
        &.lnr {
          margin: 0;
          margin-bottom: 10px;
        }
      }
    }
  }
}

// Features

// footer

footer {
  padding: 65px 0 0;
  background-image: radial-gradient(circle, #5ea565, #03653e);
  position: relative;

  > img {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
  }

  @include mob375{
    overflow: hidden;

    > img{
      transform: translateX(-50%) translateY(-205px) scale(5);
    }
  }

  @include Sm {
    padding: 40px 0 0;
  }

  .container {
    position: relative;
    z-index: 1;
  }

  .foot-links {
    // @include Lg {
    //   text-align: right;
    // }

    @include Sm {
      margin-bottom: 15px;
    }

    .collapse {
      display: block;

      @include Sm {
        display: none;
      }

      &.in {
        display: block;
      }
    }

    & > button {
      font-size: 16px;
      font-family: "tjBold";
      color: #fff;
      margin-bottom: 15px;
      background-color: transparent;
      border: 0;
      padding: 0;
      pointer-events: none;

      &.trans {
        &:after {
          content: "\2212";
        }
      }

      @include Sm {
        width: 100%;
        margin: auto;
        pointer-events: all;
        margin-bottom: 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
      }

      @include mob480 {
        width: 100%;
      }

      &:after {
        content: "\002B";
        margin-left: 5px;
        font-size: 18px;
        float: left;
        display: none;

        @include Sm {
          display: block;
        }
      }
    }

    ul {
      @include Sm {
        padding-top: 10px;
        padding-right: 10px;
      }

      li {
        &:not(:last-child) {
          margin-bottom: 10px;
        }
      }
    }

    a {
      font-size: 14px;
      font-family: "tj";
      color: #fff;
      transition: 0.5s;

      &:hover {
        color: #f1b948;
      }
    }
  }

  .newsletter {
    @include Md {
      margin-top: 35px;
      text-align: center;
    }

    @include Lg {
      margin-top: 0;
      text-align: right;

      @include LTR {
        text-align: left;
      }
    }

    @include Sm {
      margin-top: 35px;
      text-align: center;
    }

    & > p {
      font-size: 16px;
      font-family: "tjBold";
      color: #fff;
      margin-bottom: 20px;
    }

    & > span {
      font-size: 14px;
      font-family: "tj";
      color: #fff;
      margin-bottom: 15px;
      display: block;
    }
  }

  .frm {
    position: relative;
    margin-bottom: 17px;
    width: 360px;

    @include Md {
      margin: auto;
      margin-bottom: 17px;
    }

    @include Sm {
      margin: auto;
      margin-bottom: 17px;
    }

    @include mob375 {
      width: 100%;
    }

    input {
      padding: 0 20px;
      height: 45px;
      width: 100%;
      border: 0;
      color: #bbbbbb;
      border-radius: 100px;
      font-size: 14px;
      background-color: #fff;
      transition: 0.5s;

      &::placeholder {
        color: #bbbbbb;
      }
    }

    button {
      position: absolute;
      top: 50%;
      left: 6px;
      html[dir="ltr"] & {
        left: unset;
        right: 6px;
      }
      transform: translateY(-50%);
      width: 35px;
      height: 35px;
      border-radius: 50%;
      background-color: #c9ab66;
      border: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: 0.5s;

      span {
        font-size: 14px;
        color: #fff;
      }

      &:hover {
        background-color: #03653e;
      }
    }
  }

  .social {
    @include Flex;

    a {
      @include Flex;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      background-color: rgba($color-white, 0.1);
      font-size: 18px;
      transition: 0.5s;

      &:not(:last-child) {
        margin-left: 5px;

        html[dir="ltr"] & {
          margin-left: 0;
          margin-right: 5px;
        }
      }

      i {
        color: #fff;
      }

      &:hover {
        background-color: #fff;
        i {
          color: #01643e;
        }
      }
    }
  }
}

// End Of Footer

// End

.end {
  // background-image: radial-gradient(circle, #5ea565, #03653e);
  padding-top: 70px;
  position: relative;
  z-index: 1;
  .container {
    border-top: 1px solid #69a977;
    padding: 0;
  }

  .content {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include mob600 {
      flex-direction: column-reverse;
    }

    @include Xs {
      text-align: center;
    }

    p,
    a {
      font-size: 14px;
      color: #fff;
      margin-bottom: 0;

      @include Xs {
        font-size: 13px;
      }

      @include mob600 {
        margin-bottom: 10px;
      }
    }

    p {
      @include mob480 {
        display: flex;
        align-items: center;
        justify-content: center;
        // flex-direction: column;
        margin-bottom: 0;
      }
    }

    a {
      img {
        backface-visibility: hidden;
        transform: translateZ(0);
        margin-right: 5px;
        html[dir="ltr"] & {
          margin-right: 0;
          margin-left: 5px;
        }
      }

      @include mob480 {
        margin-top: 13px;
        margin-right: 3px;
        @include LTR{
          margin-right: 0;
          margin-left: 3px;
        }
      }
    }

    > img {
      backface-visibility: hidden;
      transform: translateZ(0);
    }
  }
}

// End
