.navover {
  position: fixed;
  top: 0;
  right: 0;
  width: 100vw;
  height: 100%;
  background-color: rgba(1, 100, 62, 0.5);
  z-index: 999;
  transition: 0.5s;
  opacity: 0;
  visibility: hidden;
}

.navover span {
  display: none;
  color: #fff;
  font-size: 25px;
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translateX(-50%);
}

.navover.open {
  opacity: 1;
  visibility: visible;
}

.navover.open1 {
  opacity: 1;
  visibility: visible;
  background-color: rgba(1, 100, 62, 0.95);
}

.sideNav {
  position: fixed;
  top: 0;
  right: 0;
  width: 290px;
  height: 100%;
  background-image: radial-gradient(circle, #5ea565, #03653e);
  z-index: 999;
  transition: 0.5s;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  right: -295px;
}

html[dir="ltr"] .sideNav {
  right: unset;
  left: 0;
}

html[dir="ltr"] .sideNav {
  left: -295px;
}

.sideNav > img {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.sideNav.open {
  right: 0;
}

html[dir="ltr"] .sideNav.open {
  left: 0;
}

.sideNav.open .options {
  opacity: 1;
}

.sideNav.open ul {
  opacity: 1;
}

.sideNav.open ul li {
  transform: translateY(0) !important;
  opacity: 1;
}

.sideNav .options {
  padding: 0 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  position: absolute;
  top: 50px;
  opacity: 0;
}

.sideNav .options .lang a {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  color: #fff;
  width: 32px;
  height: 32px;
  background-color: transparent;
}

.sideNav .options .close1 {
  display: flex;
  font-size: 18px;
  color: #fff;
  cursor: pointer;
}

.sideNav .options .close1 span {
  color: #fff;
}

html[dir="ltr"] .sideNav .options .close1 span {
  color: #fff;
}

.sideNav > ul {
  display: flex;
  align-items: center;
  flex-direction: column;
  opacity: 0;
  transition: 0.5s;
  transition-delay: 0.5s;
}

.sideNav > ul > li {
  position: relative;
  padding: 5px 0;
  transition: 0.5s;
  transform: translateY(10px);
  opacity: 0;
}

.sideNav > ul > li:nth-child(1) {
  transition-delay: 0.2s;
}

.sideNav > ul > li:nth-child(2) {
  transition-delay: 0.4s;
}

.sideNav > ul > li:nth-child(3) {
  transition-delay: 0.6s;
}

.sideNav > ul > li:nth-child(4) {
  transition-delay: 0.8s;
}

.sideNav > ul > li:nth-child(5) {
  transition-delay: 1s;
}

.sideNav > ul > li:nth-child(6) {
  transition-delay: 1.2s;
}

.sideNav > ul > li:nth-child(7) {
  transition-delay: 1.4s;
}

.sideNav > ul > li:nth-child(8) {
  transition-delay: 1.6s;
}

.sideNav > ul > li:nth-child(9) {
  transition-delay: 1.8s;
}

.sideNav > ul > li a {
  width: 100%;
  display: block;
  font-family: "tjMd";
  position: relative;
  font-size: 17px;
  color: #fff;
  text-align: center;
  transition: 0.5s;
}

.sideNav > ul > li .dropDown {
  display: none;
  margin-top: 10px;
}

.sideNav > ul > li .dropDown li:not(:last-child) {
  margin-bottom: 6px;
}

.sideNav > ul > li .dropDown li a {
  font-size: 15px;
  color: #e4e4e4;
}

.sideNav > ul > li .drop {
  width: 100%;
  padding: 5px 15px;
  display: none;
  overflow: visible;
  height: auto;
}

.sideNav > ul > li .drop li {
  padding: 4px 0;
}

.sideNav > ul > li .drop li a {
  font-size: 15px;
  color: #898989;
  transition: 0.5s;
}

.sideNav > ul > li .drop li a:hover {
  color: #595959;
}

